import React, { useState } from 'react';
import VideoModal from './VideoModal';
import portfolioData from '../data/portfolioData';
import './PortfolioGallery.css';

const PortfolioGallery = ({ category }) => {
  const [selectedVideo, setSelectedVideo] = useState(null);
  const [modalOpen, setModalOpen] = useState(false);

  // Get videos based on the selected category
  const videos = portfolioData[category] || [];

  // Open modal when a video is clicked
  const handleVideoClick = (video) => {
    setSelectedVideo(video);
    setModalOpen(true);
  };

  // Close the modal
  const handleCloseModal = () => {
    setModalOpen(false);
    setSelectedVideo(null);
  };

  // When no videos found for a category
  if (!videos.length) {
    return (
      <div className="no-videos">
        <p>No h vdeos disponveis para esta categoria.</p>
      </div>
    );
  }

  // Extract YouTube ID from a URL
  const getYoutubeId = (url) => {
    if (!url) return null;
    
    // For URLs like https://www.youtube.com/embed/VIDEO_ID
    if (url.includes('/embed/')) {
      return url.split('/embed/')[1].split('?')[0];
    }
    
    // For URLs like https://youtu.be/VIDEO_ID
    if (url.includes('youtu.be/')) {
      return url.split('youtu.be/')[1].split('?')[0];
    }
    
    // For URLs like https://www.youtube.com/watch?v=VIDEO_ID
    const match = url.match(/[?&]v=([^&]+)/);
    return match ? match[1] : url;
  };

  // Get YouTube thumbnail URL from video ID
  const getYoutubeThumbnail = (url) => {
    const videoId = getYoutubeId(url);
    if (!videoId) return null;
    
    // YouTube provides different thumbnail sizes:
    // default: /default.jpg (120x90)
    // medium: /mqdefault.jpg (320x180)
    // high: /hqdefault.jpg (480x360)
    // standard: /sddefault.jpg (640x480)
    // maxres: /maxresdefault.jpg (1280x720)
    return `https://img.youtube.com/vi/${videoId}/hqdefault.jpg`;
  };

  return (
    <div className="portfolio-gallery">
      <div className="videos-grid">
        {videos.map((video) => {
          // Determine which thumbnail URL to use
          const thumbnailUrl = getYoutubeThumbnail(video.videoUrl) || video.thumbnailUrl;
          
          return (
            <div key={video.id} className="video-item">
              <div 
                className="video-thumbnail" 
                onClick={() => handleVideoClick(video)}
                style={{ backgroundImage: `url(${thumbnailUrl})` }}
              >
                <div className="thumbnail-overlay">
                  <div className="play-icon">
                    <i className="fas fa-play"></i>
                  </div>
                  <span className="watch-text">Assistir Vdeo</span>
                </div>
              </div>
              <h3>{video.title}</h3>
              <p>{video.description}</p>
            </div>
          );
        })}
      </div>

      {/* Video Modal */}
      <VideoModal 
        isOpen={modalOpen}
        onClose={handleCloseModal}
        videoSrc={selectedVideo?.videoUrl}
        videoTitle={selectedVideo?.title}
        youtubeId={getYoutubeId(selectedVideo?.videoUrl)}
      />
    </div>
  );
};

export default PortfolioGallery; 