import React, { useState, useEffect } from 'react';
import { motion } from 'framer-motion';

const ModernHero = () => {
  const [backgroundVisible, setBackgroundVisible] = useState(false);
  const [textComplete, setTextComplete] = useState(false);
  
  // Main text content for typing animation
  const mainTextStart = "Transformao de ";
  const mainTextHighlight = "ideias";
  const mainTextEnd = " criativas em vdeos";
  const highlightText = "inesquecveis";
  const [displayTextStart, setDisplayTextStart] = useState("");
  const [displayTextHighlight, setDisplayTextHighlight] = useState("");
  const [displayTextEnd, setDisplayTextEnd] = useState("");
  const [displayHighlight, setDisplayHighlight] = useState("");
  const [typingComplete, setTypingComplete] = useState(false);
  
  // Typing animation effect
  useEffect(() => {
    // First part of the text
    if (displayTextStart.length < mainTextStart.length) {
      const timeout = setTimeout(() => {
        setDisplayTextStart(mainTextStart.slice(0, displayTextStart.length + 1));
      }, 30);
      return () => clearTimeout(timeout);
    } 
    // Highlighted "ideias" part
    else if (displayTextHighlight.length < mainTextHighlight.length) {
      const timeout = setTimeout(() => {
        setDisplayTextHighlight(mainTextHighlight.slice(0, displayTextHighlight.length + 1));
      }, 30);
      return () => clearTimeout(timeout);
    }
    // End part of the text
    else if (displayTextEnd.length < mainTextEnd.length) {
      const timeout = setTimeout(() => {
        setDisplayTextEnd(mainTextEnd.slice(0, displayTextEnd.length + 1));
      }, 30);
      return () => clearTimeout(timeout);
    } 
    // Final highlighted word
    else if (!typingComplete) {
      const timeout = setTimeout(() => {
        if (displayHighlight.length < highlightText.length) {
          setDisplayHighlight(highlightText.slice(0, displayHighlight.length + 1));
        } else {
          setTypingComplete(true);
          setTextComplete(true);
          // Show background after text is complete
          setTimeout(() => {
            setBackgroundVisible(true);
          }, 500);
        }
      }, 80);
      return () => clearTimeout(timeout);
    }
  }, [displayTextStart, displayTextHighlight, displayTextEnd, displayHighlight, typingComplete]);

  // Animation variants
  const containerVariants = {
    hidden: { opacity: 0 },
    visible: {
      opacity: 1,
      transition: {
        staggerChildren: 0.2,
        delayChildren: 0.3
      }
    }
  };

  const itemVariants = {
    hidden: { opacity: 0, y: 20 },
    visible: {
      opacity: 1,
      y: 0,
      transition: {
        type: "spring",
        stiffness: 100,
        damping: 15
      }
    }
  };

  const buttonVariants = {
    hidden: { opacity: 0, y: 20, scale: 0.8 },
    visible: {
      opacity: 1,
      y: 0,
      scale: 1,
      transition: {
        type: "spring",
        stiffness: 200,
        damping: 15
      }
    },
    hover: {
      scale: 1.05,
      boxShadow: "0 15px 25px rgba(220, 53, 69, 0.5)",
      y: -3,
      transition: {
        type: "spring",
        stiffness: 400,
        damping: 10
      }
    },
    tap: {
      scale: 0.98,
      y: 1
    }
  };

  const backgroundVariants = {
    hidden: { opacity: 0 },
    visible: { 
      opacity: 1,
      transition: { duration: 1.2 }
    }
  };

  return (
    <>
      <motion.div 
        className="hero-background"
        initial="hidden"
        animate={backgroundVisible ? "visible" : "hidden"}
        variants={backgroundVariants}
      />
      
      <motion.div 
        className="hero-text-wrapper"
        initial="hidden"
        animate="visible"
        variants={containerVariants}
      >
        <h1 className="modern-hero-title">
          {displayTextStart}
          {displayTextStart.length === mainTextStart.length && (
            <span className="highlight">{displayTextHighlight}</span>
          )}
          {displayTextHighlight.length === mainTextHighlight.length && displayTextEnd}
          {displayTextEnd.length === mainTextEnd.length && (
            <>
              <br />
              <span className="highlight typing-highlight">
                {displayHighlight}
              </span>
            </>
          )}
        </h1>
        
        {textComplete && (
          <motion.p 
            className="modern-hero-subtitle"
            variants={itemVariants}
          >
            Marketing estratgico e produo audiovisual de alto nvel para conectar sua marca ao pblico certo
          </motion.p>
        )}
        
        {textComplete && (
          <motion.button 
            className="btn btn-danger cta-button modern-cta"
            variants={buttonVariants}
            whileHover="hover"
            whileTap="tap"
            onClick={() => {
              document.getElementById('contact').scrollIntoView({ behavior: 'smooth' });
            }}
          >
            Entrar em contato
          </motion.button>
        )}
      </motion.div>
    </>
  );
};

export default ModernHero; 