import React, { useEffect, useRef, useState } from 'react';
import 'bootstrap/dist/css/bootstrap.min.css';
import './App.css';
import logo from './logo.svg';
import memorize_text from './memorize_text.svg';
import VideoModal from './components/VideoModal';
import heroImg from './assets/hero.png';
import CounterAnimation from './components/CounterAnimation';
import './components/CounterAnimation.css';
import ModernHero from './components/Hero/ModernHero';
import { motion } from 'framer-motion';
import PortfolioGallery from './components/PortfolioGallery';
import ImgPolitico from './assets/politico.png';
import ImgCorporativo from './assets/corporativo.png';
import ImgSocial from './assets/social.png';
import ImgComercial from './assets/comercial.png';
import emailjs from '@emailjs/browser';


// Dados do portflio com miniaturas e vdeos
const portfolioItems = [
  {
    id: 1,
    title: "Histria da Marca Corporativa",
    thumbnail: "https://images.pexels.com/photos/3184292/pexels-photo-3184292.jpeg?auto=compress&cs=tinysrgb&w=1260&h=750&dpr=2",
    videoSrc: "https://static.videezy.com/system/resources/previews/000/041/562/original/Tablet_18.mp4",
    category: "Corporativo"
  },
  {
    id: 2,
    title: "Apresentao de Produto",
    thumbnail: "https://images.pexels.com/photos/5022849/pexels-photo-5022849.jpeg?auto=compress&cs=tinysrgb&w=1260&h=750&dpr=2",
    videoSrc: "https://static.videezy.com/system/resources/previews/000/037/437/original/Typing-on-a-vintage-laptop.mp4",
    category: "Publicidade"
  },
  {
    id: 3,
    title: "Cobertura de Eventos",
    thumbnail: "https://images.pexels.com/photos/2774556/pexels-photo-2774556.jpeg?auto=compress&cs=tinysrgb&w=1260&h=750&dpr=2",
    videoSrc: "https://static.videezy.com/system/resources/previews/000/049/024/original/DSC_8271.mp4",
    category: "Eventos"
  },
  {
    id: 4,
    title: "Explicao em Motion Graphics",
    thumbnail: "https://images.pexels.com/photos/841228/pexels-photo-841228.jpeg?auto=compress&cs=tinysrgb&w=1260&h=750&dpr=2",
    videoSrc: "https://static.videezy.com/system/resources/previews/000/039/700/original/abstract-shape4k.mp4",
    category: "Motion Graphics"
  },
  {
    id: 5,
    title: "Campanha para Redes Sociais",
    thumbnail: "https://images.pexels.com/photos/607812/pexels-photo-607812.jpeg?auto=compress&cs=tinysrgb&w=1260&h=750&dpr=2",
    videoSrc: "https://static.videezy.com/system/resources/previews/000/049/383/original/DSC_8420.mp4",
    category: "Publicidade"
  },
  {
    id: 6,
    title: "Curta-metragem Cinematogrfico",
    thumbnail: "https://images.pexels.com/photos/3062541/pexels-photo-3062541.jpeg?auto=compress&cs=tinysrgb&w=1260&h=750&dpr=2",
    videoSrc: "https://static.videezy.com/system/resources/previews/000/042/692/original/MACAO-CITY.mp4",
    category: "Corporativo"
  }
];

// Dados estatsticos com contadores
const statsData = [
  { id: 1, value: 357, suffix: "+", label: "Projetos Realizados" },
  { id: 2, value: 82, suffix: "+", label: "Clientes Atendidos" },
  { id: 3, value: 527, suffix: "+", label: "Dirias de Trabalho" },
  { id: 4, value: 82, suffix: "%", label: "Clientes Recorrentes" }
];

// Define playlists categories
const playlistCategories = [
  { id: "institucionais", title: "INSTITUCIONAIS" },
  { id: "comerciais", title: "COMERCIAIS" },
  { id: "historias", title: "HISTRIAS REAIS" },
  { id: "redes", title: "REDES SOCIAIS" },
  { id: "politico", title: "MARKETING POLTICO" },
  { id: "eventos", title: "EVENTOS" }
];

function App() {
  const videoRef = useRef(null);
  const formRef = useRef(null);
  const [activeCategory, setActiveCategory] = useState("Todos");
  const [modalOpen, setModalOpen] = useState(false);
  const [currentVideo, setCurrentVideo] = useState(null);
  const [selectedPlaylist, setSelectedPlaylist] = useState(playlistCategories[0].id);
  const [formData, setFormData] = useState({
    name: '',
    email: '',
    whatsapp: '',
    subject: '',
    message: ''
  });
  const [formStatus, setFormStatus] = useState({
    submitting: false,
    submitted: false,
    error: null
  });
  const [validationErrors, setValidationErrors] = useState({});
  
  // State to track whether the user is in the hero section
  const [isInHeroSection, setIsInHeroSection] = useState(true);
  
  // Initialize EmailJS
  useEffect(() => {
    // Initialize EmailJS with your public key
    emailjs.init("rTFp4zS-fXtLK4p4V"); // Replace with your actual public key
  }, []);
  
  // Configurao de observadores de interseo para gatilhos de animao
  useEffect(() => {
    // Observador para sees
    const sectionObserver = new IntersectionObserver((entries) => {
      entries.forEach(entry => {
        if (entry.isIntersecting) {
          entry.target.classList.add('in-view');
        } else {
          entry.target.classList.remove('in-view');
        }
      });
    }, { threshold: 0.2 });

    // Observador para cards e outros elementos que animam ao entrar
    const cardObserver = new IntersectionObserver((entries) => {
      entries.forEach(entry => {
        if (entry.isIntersecting) {
          entry.target.classList.add('visible');
        }
      });
    }, { threshold: 0.1, rootMargin: '0px 0px -50px 0px' });

    // Observar todas as sees
    const sections = document.querySelectorAll('.section');
    sections.forEach(section => {
      sectionObserver.observe(section);
    });

    // Observar todos os cards animados
    const cards = document.querySelectorAll('.portfolio-item, .service-card, .stat-card, .testimonial-card');
    cards.forEach(card => {
      cardObserver.observe(card);
    });

    // Efeito parallax para o vdeo de fundo
    const handleScroll = () => {
      if (videoRef.current) {
        const scrollPosition = window.scrollY;
        videoRef.current.style.transform = `translateY(${scrollPosition * 0.3}px) scale(1.1)`;
      }
      
      // Check if user is in hero section (top of page)
      setIsInHeroSection(window.scrollY < 100);
    };

    window.addEventListener('scroll', handleScroll);

    return () => {
      sectionObserver.disconnect();
      cardObserver.disconnect();
      window.removeEventListener('scroll', handleScroll);
    };
  }, []);

  // Filtrar itens do portflio com base na categoria ativa
  const filteredPortfolioItems = portfolioItems.filter(item => 
    activeCategory === "Todos" || item.category === activeCategory
  );

  // Lidar com clique no vdeo
  const handleVideoClick = (item) => {
    setCurrentVideo(item);
    setModalOpen(true);
  };

  // Lidar com fechamento do modal
  const handleCloseModal = () => {
    setModalOpen(false);
    setCurrentVideo(null);
  };

  const handlePlaylistSelect = (playlistId) => {
    setSelectedPlaylist(playlistId);
  };

  // Handle form input changes
  const handleInputChange = (e) => {
    const { name, value } = e.target;
    setFormData({
      ...formData,
      [name]: value
    });
    
    // Clear validation errors when user types
    if (validationErrors[name]) {
      setValidationErrors({
        ...validationErrors,
        [name]: null
      });
    }
  };
  
  // Validate form data
  const validateForm = () => {
    const errors = {};
    
    if (!formData.name.trim()) {
      errors.name = "Nome  obrigatrio";
    }
    
    if (!formData.email.trim()) {
      errors.email = "Email  obrigatrio";
    } else if (!/\S+@\S+\.\S+/.test(formData.email)) {
      errors.email = "Email invlido";
    }
    
    if (!formData.whatsapp.trim()) {
      errors.whatsapp = "WhatsApp  obrigatrio";
    } else if (!/^[0-9]{10,11}$/.test(formData.whatsapp.replace(/\D/g, ''))) {
      errors.whatsapp = "Nmero de WhatsApp invlido";
    }
    
    if (!formData.subject.trim()) {
      errors.subject = "Assunto  obrigatrio";
    }
    
    if (!formData.message.trim()) {
      errors.message = "Mensagem  obrigatria";
    } else if (formData.message.trim().length < 10) {
      errors.message = "Mensagem muito curta";
    }
    
    setValidationErrors(errors);
    return Object.keys(errors).length === 0;
  };
  
  // Handle form submission
  const handleSubmit = (e) => {
    e.preventDefault();
    
    // Validate form before submission
    if (!validateForm()) {
      return;
    }
    
    setFormStatus({
      submitting: true,
      submitted: false,
      error: null
    });
    
    // EmailJS service details
    const serviceId = 'service_ms117kf'; // Replace with your EmailJS service ID
    const templateId = 'template_4y9am3h'; // Replace with your EmailJS template ID
    
    // Send email using form reference
    emailjs.sendForm(serviceId, templateId, formRef.current)
      .then((response) => {
        console.log('Email sent successfully!', response);
        setFormStatus({
          submitting: false,
          submitted: true,
          error: null
        });
        
        // Reset form
        setFormData({
          name: '',
          email: '',
          whatsapp: '',
          subject: '',
          message: ''
        });
        
        // Show success message (no alert to improve UX)
        window.scrollTo({
          top: document.getElementById('contact').offsetTop - 100,
          behavior: 'smooth'
        });
        
        // Reset form status after 5 seconds
        setTimeout(() => {
          setFormStatus(prev => ({
            ...prev,
            submitted: false
          }));
        }, 5000);
      })
      .catch((error) => {
        console.error('Error sending email:', error);
        setFormStatus({
          submitting: false,
          submitted: false,
          error: 'Ocorreu um erro ao enviar a mensagem. Por favor, tente novamente.'
        });
      });
  };

  return (
    <div className="App">
      <VideoModal 
        isOpen={modalOpen}
        onClose={handleCloseModal}
        videoSrc={currentVideo?.videoSrc}
        videoTitle={currentVideo?.title}
      />
      
      <header className={`navbar navbar-expand-lg navbar-dark fixed-top ${isInHeroSection ? 'header-minimized' : ''}`}>
        <div className="container">
          <div 
            className={`logo ${isInHeroSection ? 'logo-centered' : ''}`}
            onClick={() => {
              if (isInHeroSection) {
                // When in hero section, logo click scrolls to top
                window.scrollTo({ top: 0, behavior: 'smooth' });
              }
            }}
            style={{ cursor: isInHeroSection ? 'pointer' : 'default' }}
          >
            <img src={logo} href="#hero" alt="Memorize Produes Logo" />
            <img src={memorize_text} href="#hero" alt="Memorize Produes Logo" style={{ width: '100px' }}/>
          </div>
          
          {!isInHeroSection && (
            <>
              <div className="" href="#hero">
                <img src={memorize_text} alt="Memorize Produes" />
              </div>
               
              <button className="navbar-toggler" type="button" data-bs-toggle="collapse" data-bs-target="#navbarNav" aria-controls="navbarNav" aria-expanded="false" aria-label="Toggle navigation">
                <span className="navbar-toggler-icon"></span>
              </button>
               
              <nav className="collapse navbar-collapse justify-content-end" id="navbarNav" aria-label="Navegao principal">
                <ul className="navbar-nav">
                  <li className="nav-item"><a className="nav-link" href="#portfolio">Portflio</a></li>
                  <li className="nav-item"><a className="nav-link" href="#services">Servios</a></li>
                  <li className="nav-item"><a className="btn btn-danger cta-button" href="#contact">Contato</a></li>
                </ul>
              </nav>
            </>
          )}
        </div>
      </header>
      
      <main className="main-content">
        {/* Seo Hero */}
        <section className="section hero-section" id="hero" aria-label="Seo principal">
          <div className="container h-100">
            <div className="row h-100 align-items-center">
              <div className="col-12 col-md-6">
                <motion.div 
                  className="hero-content modern-hero-content"
                  initial={{ opacity: 0, x: -100 }}
                  animate={{ opacity: 1, x: 0 }}
                  transition={{ 
                    duration: 0.8, 
                    type: "spring",
                    stiffness: 100,
                    damping: 20
                  }}
                >
                  <ModernHero />
                </motion.div>
              </div>
            </div>
          </div>
          <div className="video-background">
            <div className="overlay"></div>
            <img
              src={heroImg}
              alt="Produes de vdeo profissional - background"
              className="hero-video"
            />
          </div>
        </section>

        {/* About Us Section 
        <AboutUs />
        */}

        {/* Seo de Portflio */}
        <section className="section portfolio-section" id="portfolio" aria-label="Portflio">
          <div className="container">
            <div className="section-header text-left">
              <h2>Nosso Portflio</h2>
            </div>
            
            <div className="portfolio-tabs">
              {playlistCategories.map((category) => (
                <button 
                  key={category.id}
                  className={`tab ${selectedPlaylist === category.id ? "active" : ""}`}
                  onClick={() => setSelectedPlaylist(category.id)}
                  aria-pressed={selectedPlaylist === category.id}
                  aria-label={`Categoria ${category.title}`}
                >
                  {category.title}
                </button>
              ))}
            </div>
            
            <div className="youtube-playlist-wrapper">
              {selectedPlaylist && <PortfolioGallery category={selectedPlaylist} />}
            </div>
          </div>

          <div className="" id="action">
            <div className="container">
              <div className="d-flex justify-content-center">
                <a className="btn btn-danger cta-button" href="#contact" onClick={(e) => {
                  e.preventDefault();
                  document.getElementById('contact').scrollIntoView({ behavior: 'smooth' });
                }}>Entrar em Contato</a>
              </div>
            </div>
          </div>
        </section>

        {/* Seo de Servios */}
        <section className="section services-section" id="services" aria-label="Nossos Servios">
          <div className="container">
            <div className="section-header text-left">
              <h2>Nossos Servios</h2>
            </div>
            <div className="row services-grid">
              <div className="col-12 col-md-6 col-lg-3 mb-4">
                <article className="service-card">
                  <div className="service-card-bg" style={{ 
                    backgroundImage: `url(${ImgCorporativo})`
                  }}></div>
                  
                  <div className="service-card-header">
                    <div className="service-icon">
                      <i className="fas fa-video" aria-hidden="true"></i>
                    </div>
                    <h3 className="service-title">Produo de Vdeos Corporativos</h3>
                  </div>
                  
                  <div className="service-description">Criamos vdeos corporativos que contam a histria da sua empresa de maneira envolvente e informativa.</div>
                </article>
              </div>
              <div className="col-12 col-md-6 col-lg-3 mb-4">
                <article className="service-card">
                  <div className="service-card-bg" style={{ 
                    backgroundImage: `url(${ImgSocial})`
                  }}></div>
                  
                  <div className="service-card-header">
                    <div className="service-icon">
                      <i className="fas fa-share-alt" aria-hidden="true"></i>
                    </div>
                    <h3 className="service-title">Produo de Contedo para Mdias Sociais</h3>
                  </div>
                  
                  <div className="service-description">Desenvolvemos vdeos e animaes para impulsionar a presena digital da sua marca.</div>
                </article>
              </div>
              <div className="col-12 col-md-6 col-lg-3 mb-4">
                <article className="service-card">
                  <div className="service-card-bg" style={{ 
                    backgroundImage: `url(${ImgPolitico})`
                  }}></div>
                  
                  <div className="service-card-header">
                    <div className="service-icon">
                      <i className="fas fa-bullhorn" aria-hidden="true"></i>
                    </div>
                    <h3 className="service-title">Produo de Vdeos para Marketing Poltico</h3>
                  </div>
                  
                  <div className="service-description">Produzimos vdeo para marketing poltico criando contedos estratgicos para campanhas, engajando eleitores, transmitindo propostas e fortalecendo a imagem do candidato nas redes e na TV.</div>
                </article>
              </div>
              <div className="col-12 col-md-6 col-lg-3 mb-4">
                <article className="service-card">
                  <div className="service-card-bg" style={{ 
                    backgroundImage: `url(${ImgComercial})`
                  }}></div>
                  
                  <div className="service-card-header">
                    <div className="service-icon">
                      <i className="fas fa-ad" aria-hidden="true"></i>
                    </div>
                    <h3 className="service-title">Produo de Comerciais</h3>
                  </div>
                  
                  <div className="service-description">Em parceria com grandes agncias de publicidade, transformamos roteiros criativos em vdeos comerciais inovadores.</div>
                </article>
              </div>
            </div>
            <button className="btn btn-danger cta-button" onClick={() => {
              document.getElementById('contact').scrollIntoView({ behavior: 'smooth' });
            }}>Solicitar Oramento</button>
          </div>
        </section>
        
        
        
        {/* Seo de Estatsticas */}
        <section className="section stats-section" id="stats" aria-label="Nossos Nmeros">
          <div className="container">
            <div className="section-header text-left">
              <h2>Nossos Nmeros</h2>
            </div>
            <div className="row stats-grid">
              {statsData.map((stat) => (
                <div key={stat.id} className="col-6 col-md-3 mb-4">
                  <div className="stat-card">
                    <h3>
                      <CounterAnimation 
                        end={stat.value} 
                        duration={2000} 
                        suffix={stat.suffix} 
                      />
                    </h3>
                    <p>{stat.label}</p>
                  </div>
                </div>
              ))}
            </div>
          </div>
        </section>
        
        {/* Seo de Contato */}
        <section className="section contact-section" id="contact" aria-label="Contato">
          <div className="container">
            <div className="section-header text-left">
              <h2>Solicitar Oramento</h2>
            </div>
            <div className="row justify-content-center">
              <div className="col-12 col-md-10 col-lg-8">
                <div className="contact-container">
                  {formStatus.submitted ? (
                    <div className="success-message">
                      <div className="checkmark-wrapper">
                        <div className="checkmark" aria-hidden="true"></div>
                      </div>
                      <h3>Mensagem Enviada!</h3>
                      <p>Obrigado pelo seu contato. Retornaremos em breve.</p>
                      <button 
                        className="btn btn-outline-danger mt-3"
                        onClick={() => setFormStatus({...formStatus, submitted: false})}
                      >
                        Enviar nova mensagem
                      </button>
                    </div>
                  ) : (
                    <form className="contact-form" onSubmit={handleSubmit} ref={formRef} aria-label="Formulrio de contato">
                      <div className="mb-3">
                        <label htmlFor="name" className="visually-hidden">Nome</label>
                        <input 
                          id="name"
                          type="text" 
                          className={`form-control ${validationErrors.name ? 'is-invalid' : ''}`}
                          placeholder="Nome" 
                          name="name"
                          value={formData.name}
                          onChange={handleInputChange}
                          required
                          aria-required="true"
                          aria-invalid={validationErrors.name ? 'true' : 'false'}
                        />
                        {validationErrors.name && (
                          <div className="invalid-feedback">{validationErrors.name}</div>
                        )}
                      </div>
                      <div className="mb-3">
                        <label htmlFor="email" className="visually-hidden">Email</label>
                        <input 
                          id="email"
                          type="email" 
                          className={`form-control ${validationErrors.email ? 'is-invalid' : ''}`}
                          placeholder="Email" 
                          name="email"
                          value={formData.email}
                          onChange={handleInputChange}
                          required
                          aria-required="true"
                          aria-invalid={validationErrors.email ? 'true' : 'false'}
                        />
                        {validationErrors.email && (
                          <div className="invalid-feedback">{validationErrors.email}</div>
                        )}
                      </div>
                      <div className="mb-3">
                        <label htmlFor="whatsapp" className="visually-hidden">WhatsApp</label>
                        <input 
                          id="whatsapp"
                          type="tel" 
                          className={`form-control ${validationErrors.whatsapp ? 'is-invalid' : ''}`}
                          placeholder="WhatsApp" 
                          name="whatsapp"
                          value={formData.whatsapp}
                          onChange={handleInputChange}
                          required
                          aria-required="true"
                          aria-invalid={validationErrors.whatsapp ? 'true' : 'false'}
                        />
                        {validationErrors.whatsapp && (
                          <div className="invalid-feedback">{validationErrors.whatsapp}</div>
                        )}
                      </div>
                      <div className="mb-3">
                        <label htmlFor="subject" className="visually-hidden">Assunto</label>
                        <input 
                          id="subject"
                          type="text" 
                          className={`form-control ${validationErrors.subject ? 'is-invalid' : ''}`}
                          placeholder="Assunto" 
                          name="subject"
                          value={formData.subject}
                          onChange={handleInputChange}
                          required
                          aria-required="true"
                          aria-invalid={validationErrors.subject ? 'true' : 'false'}
                        />
                        {validationErrors.subject && (
                          <div className="invalid-feedback">{validationErrors.subject}</div>
                        )}
                      </div>
                      <div className="mb-3">
                        <label htmlFor="message" className="visually-hidden">Mensagem</label>
                        <textarea 
                          id="message"
                          className={`form-control ${validationErrors.message ? 'is-invalid' : ''}`}
                          placeholder="Mensagem" 
                          rows="5"
                          name="message"
                          value={formData.message}
                          onChange={handleInputChange}
                          required
                          aria-required="true"
                          aria-invalid={validationErrors.message ? 'true' : 'false'}
                        ></textarea>
                        {validationErrors.message && (
                          <div className="invalid-feedback">{validationErrors.message}</div>
                        )}
                      </div>
                      {formStatus.error && (
                        <div className="alert alert-danger mb-3" role="alert">
                          {formStatus.error}
                        </div>
                      )}
                      <button 
                        type="submit" 
                        className="btn btn-danger cta-button"
                        disabled={formStatus.submitting}
                        aria-busy={formStatus.submitting ? 'true' : 'false'}
                      >
                        {formStatus.submitting ? (
                          <span>
                            <span className="spinner-border spinner-border-sm me-2" role="status" aria-hidden="true"></span>
                            Enviando...
                          </span>
                        ) : (
                          'Enviar Mensagem'
                        )}
                      </button>
                    </form>
                  )}
                  
                  <div className="social-buttons mt-4">
                    <p className="text-center mb-3">Ou entre em contato diretamente:</p>
                    <div className="d-flex justify-content-center gap-3">
                      <a href="https://wa.me/5511975523737" className="btn btn-success social-btn" target="_blank" rel="noopener noreferrer" aria-label="Contato via WhatsApp">
                        <i className="fab fa-whatsapp me-2" aria-hidden="true"></i>WhatsApp
                      </a>
                      <a href="https://instagram.com/memorize_mktdigital" className="btn btn-instagram social-btn" target="_blank" rel="noopener noreferrer" aria-label="Perfil no Instagram">
                        <i className="fab fa-instagram me-2" aria-hidden="true"></i>Instagram
                      </a>
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </section>
      </main>

      {/* Footer 
      <Footer />
      */}
      
      <a href="https://wa.me/5511999999999" className="whatsapp-float" target="_blank" rel="noopener noreferrer" aria-label="Contato via WhatsApp">
        <i className="fab fa-whatsapp" aria-hidden="true"></i>
      </a>
    </div>
  );
}

export default App;
